<template>
  <div>
    <ModalAddParticipante />
    <!-- Breadcrumb -->
    <div class="d-flex justify-content-start align-items-center">
      <b-button
        class="mr-1"
        variant="outline-primary"
        style="padding: 5px 10px"
        :to="{ name: 'actividades-deportivasx'}"
      >
        <feather-icon
          icon="ChevronsLeftIcon"
        />
        volver
      </b-button>
      <span class="border-right pr-1 mr-1"><h3>Servicio Deportivo</h3></span>
      <feather-icon
        icon="HomeIcon"
        class="text-primary"
        size="16"
      />
      <feather-icon
        icon="ChevronRightIcon"
      />
      <b-breadcrumb
        :items="itemsBreadcrumb"
      />
    </div>
    <br>
    <!-- Breadcrumb End -->
    <form-wizard
      next-button-text="Siguiente"
      back-button-text="Anterior"
      color="#06634c"
      :title="null"
      :subtitle="null"
      layout="vertical"
      finish-button-text="Terminar Proceso"
      class="wizard-vertical mb-3 p-1"
      @on-complete="formSubmitted"
      @on-change="onchange"
    >
      <tab-content
        title="Crear Actividad"
        :before-change="validationFormInfo"
      >
        <validation-observer
          ref="infoRules"
          tag="form"
        >
          <b-row>
            <b-col
              cols="12"
              class="mb-2"
            >
              <h5 class="mb-0">
                Registro de nueva actividad
              </h5>
              <small class="text-muted">
                Detalle la actividad a crear
              </small>
            </b-col>
            <b-col md="8">
              <b-form-group
                label="Actividad"
                label-for="v-actividad"
              >
                <validation-provider
                  #default="{ errors }"
                  name="actividad"
                  rules="required"
                >
                  <b-form-input
                    id="v-actividad"
                    v-model="actividad.nombre"
                    :state="errors.length > 0 ? false:null"
                    placeholder=""
                  />
                  <small
                    v-if="errors.length>0"
                    class="text-danger"
                  >Este campo es obligatorio</small>
                </validation-provider>
              </b-form-group>
            </b-col>
            <b-col md="4">
              <b-form-group

                label="Fecha"
                label-for="v-fecha"
              >
                <validation-provider
                  #default="{ errors }"
                  name="fecha"
                  rules="required"
                >
                  <b-form-input
                    id="v-fecha"
                    v-model="actividad.fecha"
                    type="date"
                    :state="errors.length > 0 ? false:null"
                  />
                  <small
                    v-if="errors.length>0"
                    class="text-danger"
                  >Este campo es obligatorio</small>
                </validation-provider>
              </b-form-group>
            </b-col>
            <b-col md="12">
              <b-form-group
                label="Descripcin (opcional)"
                label-for="v-descripcion"
              >
                <b-form-textarea
                  id="v-descripcion"
                  v-model="actividad.descripcion"
                  placeholder="Descripcin de la actividad"
                />
              </b-form-group>
            </b-col>
          </b-row>
        </validation-observer>
      </tab-content>
      <tab-content title="Participantes">
        <b-row>
          <b-col
            cols="12"
            class="mb-2"
          >
            <h5 class="mb-0">
              Participantes
            </h5>
            <small class="text-muted">Aada a los participantes de la actividad</small>
          </b-col>
          <b-col md="6">
            <b-button
              variant="primary"
              @click="$bvModal.show('modalAddParticipante')"
            >
              <feather-icon icon="UsersIcon" />
              Adicionar participantes
            </b-button>
          </b-col>
          <b-col
            md="6"
            class="my-1 float-right"
          >
            <b-form-group
              class="mb-0"
            >
              <b-input-group size="sm">
                <b-form-input
                  id="filter-input"
                  v-model="filter"
                  type="search"
                  placeholder="Buscar por DNI o Apellidos y nombres"
                />

                <b-input-group-append>
                  <b-button
                    :disabled="!filter"
                    size=""
                    @click="filter = ''"
                  >
                    Limpiar
                  </b-button>
                </b-input-group-append>
              </b-input-group>
            </b-form-group>
          </b-col>
          <b-col md="12">
            <b-table
              :items="participantes"
              :fields="fieldParticipantes"
              :filter="filter"
              sticky-header="300px"
              small
              head-variant="light"
              class="mb-3"
            >
              <template #cell(index)="row">
                {{ row.index + 1 }}
              </template>
              <template #cell(actions)="row">
                <b-button
                  size="sm"
                  class="mr-1"
                  variant="outline-danger"
                  @click="eliminarParticipante(row.item.id)"
                >
                  eliminar
                </b-button>
              </template>
              <template #cell(participante)="data">

                <b-media vertical-align="center">
                  <template #aside>
                    <b-avatar
                      size="32"
                      :variant="`light-primary`"
                      :text="avatarText(data.item.participante.nombres)"
                    />
                  </template>
                  <b-link
                    class="font-weight-bold d-block text-nowrap"
                  >
                    {{ data.item.participante.apellido_paterno }} {{ data.item.participante.apellido_materno }} {{ data.item.participante.nombres }}
                  </b-link>
                  <small class="text-muted">N {{ data.item.dni }}</small>
                </b-media>
              </template>
            </b-table>
            <br>
            <br>
          </b-col>
        </b-row>
      </tab-content>
      <!-- address -->
      <tab-content title="Evidencias">
        <b-row>
          <b-col
            cols="12"
            class="mb-2"
          >
            <h5 class="mb-0">
              Evidencias
            </h5>
            <small class="text-muted">Registro de evidencias</small>
          </b-col>
          <b-col md="5">
            <b-form-group
              label="Nombre Evidencia (opcional)"
              label-for="nueva_evidencia"
            >
              <b-form-input
                id="nueva_evidencia"
                v-model="evidenciaNueva.nombre"
                size="sm"
              />
            </b-form-group>
          </b-col>
          <b-col md="4">
            <b-form-group
              label="Archivo"
              label-for="archivo"
            >
              <b-form-file
                v-model="evidenciaNueva.archivo"
                size="sm"
              />
            </b-form-group>
          </b-col>
          <b-col md="3">
            <b-button
              variant="success"
              size="sm"
              class="mt-2"
              @click="adicionarArchivo()"
              v-text="'Add archivo'"
            />
          </b-col>
          <b-col md="12">
            <b-table
              :items="evidencias"
              :fields="fieldEvidencias"
              sticky-header="300px"
              small
              head-variant="light"
              class="mb-3"
            >
              <template #cell(actions)="row">
                <b-button
                  size="sm"
                  class="mr-1"
                  variant="outline-danger"
                  @click="eliminarEvidencia(row.item.id)"
                >
                  eliminar <feather-icon icon="Trash2Icon" />
                </b-button>
              </template>
              <template #cell(archivo)="row">
                <a
                  :href="row.item.archivo"
                  class="btn btn-sm btn-outline-success"
                  target="_blank"
                >ver
                  <feather-icon icon="EyeIcon" />
                </a>
              </template>
            </b-table>
            <br>
            <br>
          </b-col>
        </b-row>
      </tab-content>
    </form-wizard>

  </div>
</template>

<script>
import { FormWizard, TabContent } from 'vue-form-wizard'
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import { required } from '@validations'
import 'vue-form-wizard/dist/vue-form-wizard.min.css'
import {
  BRow, BMedia, BLink, BCol, BFormGroup, BFormInput, BFormTextarea, BButton, BTable,
  BInputGroupAppend, BInputGroup, BFormFile, BAvatar, BBreadcrumb,
} from 'bootstrap-vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
// import store from '@/store'
import store from '@/store'
import { onUnmounted } from '@vue/composition-api'
import { mapState } from 'vuex'
import { avatarText } from '@core/utils/filter'
import FeatherIcon from '@/@core/components/feather-icon/FeatherIcon.vue'
import ModalAddParticipante from './ModalAddParticipante.vue'
import actDeportivasStoreModule from './actDeportivasStoreModule'

export default {
  components: {
    BAvatar,
    BMedia,
    BLink,
    ModalAddParticipante,
    ValidationProvider,
    ValidationObserver,
    // BCard,
    FormWizard,
    TabContent,
    BRow,
    BCol,
    BTable,
    BInputGroupAppend,
    BInputGroup,
    BButton,
    BFormGroup,
    BFormInput,
    BFormTextarea,
    BFormFile,
    BBreadcrumb,
    // eslint-disable-next-line vue/no-unused-components
    ToastificationContent,
    FeatherIcon,
  },

  data() {
    return {
      avatarText,
      actividad_id: this.$attrs.id,
      evidenciaNueva: {
        nombre: '',
        archivo: null,
      },
      esNuevo: true,
      fieldEvidencias: [
        {
          key: 'nombre', label: 'Nombre', sortable: true, sortDirection: 'asc',
        },
        {
          key: 'archivo', label: 'Evidencia', sortable: true, sortDirection: 'asc',
        },
        {
          key: 'actions', label: 'Eliminar', sortable: true, sortDirection: 'asc',
        },
      ],
      fieldParticipantes: [
        { key: 'index', label: 'N' },
        {
          key: 'participante', label: 'Participante', sortable: true, sortDirection: 'asc',
        },
        {
          key: 'pueblo_indigena', label: 'Pueblo Indigena', sortable: true, sortDirection: 'asc',
        },
        { key: 'actions', label: 'Retirar' },
      ],
      filter: null,
      // participantes: [],
      required,
      actividad: {
        id: '',
        nombre: '',
        fecha: '',
        descripcion: '',
      },

      itemsBreadcrumb: [
        {
          text: 'Actividades Deportivas',
          to: { name: 'actividades-deportivasx' },
        },
        {
          text: 'Actividad',
          active: true,
        },
      ],
    }
  },
  computed: {
    ...mapState('actividades_depor', ['participantes', 'evidencias', 'tabla', 'talleresSegunDeporte', 'tituloModal', 'tituloBoton', 'guardarEditar', 'cursoTallerNuevo']),
    sortOptions() {
      // Create an options list from our fields
      return this.fields
        .filter(f => f.sortable)
        .map(f => ({ text: f.label, value: f.key }))
    },
  },
  mounted() {
    store.state.actividades_depor.actividad_id = this.$attrs.id
    store.dispatch('actividades_depor/fetchParticipantes', this.actividad_id)
    store.dispatch('actividades_depor/fetchEvidencias', this.actividad_id)

    store.dispatch('actividades_depor/getActividad', this.actividad_id).then(response => {
      this.actividad.id = this.actividad_id
      this.actividad.nombre = response.data.nombre
      this.actividad.fecha = response.data.fecha
      this.actividad.descripcion = response.data.descripcion
      //  this.swalSuccess('Actividad actualizada!')
    }).catch(error => {
      console.log(error)
    })
  },
  methods: {
    adicionarArchivo() {
      if (this.evidenciaNueva.nombre === '' || this.evidenciaNueva.archivo === null) {
        this.toast('Campo nombre o archivo vacio ', 'warning', 'Advertencia')
        return
      }
      const formData = new FormData()
      formData.append('id', this.actividad_id)
      formData.append('nombre', this.evidenciaNueva.nombre)
      formData.append('archivo', this.evidenciaNueva.archivo)
      store.dispatch('actividades_depor/addEvidencia', formData).then(() => {
        // store.dispatch('actividades_depor/fetchParticipantes', this.actividad_id)
        this.toast('Evidencia registrado!', 'success', 'xito')
        store.dispatch('actividades_depor/fetchEvidencias', this.actividad_id)
        this.evidenciaNueva = []
      }).catch(error => {
        console.log(error)
      })
    },
    eliminarEvidencia(id) {
      store.dispatch('actividades_depor/deleteEvidencia', id).then(res => {
        this.swalSuccess(`Evidencia ${res.data.data.nombre} eliminada`)
        store.dispatch('actividades_depor/fetchEvidencias', this.actividad_id)
      })
    },
    eliminarParticipante(id) {
      store.dispatch('actividades_depor/deleteParticipante', id).then(() => {
        store.dispatch('actividades_depor/fetchParticipantes', this.actividad_id)
        this.toast('Participante retirado!', 'success', 'xito')
      }).catch(error => {
        console.log(error)
      })
    },
    validationFormInfo() {
      return new Promise((resolve, reject) => {
        this.$refs.infoRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
    toast(text, tipo, titulo) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title: titulo,
          icon: 'BellIcon',
          text,
          variant: tipo,
        },
      })
    },
    formSubmitted() {
      this.$toast({
        component: ToastificationContent,
        props: {
          title: 'Actividad creada/actualizada con xito!',
          icon: 'EditIcon',
          variant: 'success',
        },
      })
      this.$router.push({ name: 'actividades-deportivasx' })
    },
    swalSuccess(texto) {
      this.$swal({
        position: 'top-end',
        icon: 'success',
        title: texto,
        showConfirmButton: false,
        timer: 1500,
        customClass: {
          confirmButton: 'btn btn-primary',
        },
        buttonsStyling: false,
      })
    },
    // eslint-disable-next-line no-unused-vars
    onchange(prevIndex) {
      switch (prevIndex) {
        case 0: this.guardarActualizar()
          break
        default:
          break
      }
    },
    guardarActualizar() {
      if (parseInt(this.actividad_id, 10) === 0) { // true : Registra nueva actividad
        store.dispatch('actividades_depor/addActividad', this.actividad).then(response => {
          this.esNuevo = false
          this.actividad_id = response.data.id
          store.state.actividades_depor.actividad_id = response.data.id
        }).catch(error => {
          console.log(error)
        })
      } else { // false: Actualizamos actividad
        store.dispatch('actividades_depor/updateActividad', this.actividad).then(() => {

        }).catch(error => {
          console.log(error)
        })
      }
    },
  },
  setup() {
    const ACTIVIDADES_DEPOR_APP_STORE_MODULE_NAME = 'actividades_depor'

    // Register module
    if (!store.hasModule(ACTIVIDADES_DEPOR_APP_STORE_MODULE_NAME)) store.registerModule(ACTIVIDADES_DEPOR_APP_STORE_MODULE_NAME, actDeportivasStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(ACTIVIDADES_DEPOR_APP_STORE_MODULE_NAME)) store.unregisterModule(ACTIVIDADES_DEPOR_APP_STORE_MODULE_NAME)
    })

    return {

    }
  },
}
</script>
<style lang="scss">
  @import '@core/scss/vue/libs/vue-wizard.scss';
  @import '@core/scss/vue/libs/vue-select.scss';
</style>
<style scoped>
 .wizard-icon-container{
   background-color: red;
 }
</style>
