// import store from "@/store"
import axios from '@axios'

export default {
  namespaced: true,
  state: {
    actividad_id: 0,
    tabla: [],
    participantes: [], // participante_id, DNI, PARTICIPANTE, EMAIL, PUEBLO_INDIGENA
    evidencias: [], // id, nombre, archivo
    actividadesDeportivas: [], // id,  nombre, fecha, descripcion
    guardarEditar: true,
  },
  getters: {},
  mutations: {
    listarParticipantes(state, data) {
      const datax = []
      data.forEach(val => {
        datax.push(
          {
            id: val.id,
            participante: val.persona,
            dni: val.persona.numero_documento,
            participante_id: val.participante_id,
            pueblo_indigena: val.persona.pueblo_indigena.nombre,
          },
        )
      })
      state.participantes = datax
    },
    listarEvidencias(state, data) {
      state.evidencias = data
    },

  },
  actions: {
    fetchActividades(ctx, { queryParams }) {
      return new Promise((resolve, reject) => {
        axios
          .get('/servicio-deportivo/actividad', { params: queryParams })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    getActividad(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-deportivo/actividad/${id}`)
          .then(response => { resolve(response) })
          .catch(error => reject(error))
      })
    },
    addActividad(ctx, data) {
      return new Promise((resolve, reject) => {
        axios
          .post('/servicio-deportivo/actividad', data)
          .then(response => { resolve(response) })
          .catch(error => reject(error))
      })
    },
    updateActividad(ctx, data) {
      return new Promise((resolve, reject) => {
        axios
          .put(`/servicio-deportivo/actividad/${data.id}`, data)
          .then(response => { resolve(response) })
          .catch(error => reject(error))
      })
    },
    deleteActividad(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .delete(`/servicio-deportivo/actividad/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    searchParticipante(ctx, cadenaBuscar) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/buscar-persona2?query=${cadenaBuscar}`, { withCredentials: false })
          .then(response => { resolve(response) })
          .catch(error => reject(error))
      })
    },

    /* PARTICIPANTES */
    fetchParticipantes(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-deportivo/actividad/${id}/participante`)
          .then(response => { ctx.commit('listarParticipantes', response.data); resolve(response) })
          .catch(error => { reject(error) })
      })
    },
    addParticipante(ctx, data) {
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-deportivo/actividad/${data.actividad_id}/participante`, data)
          .then(response => { resolve(response) })
          .catch(error => reject(error))
      })
    },
    deleteParticipante(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .delete(`/servicio-deportivo/actividad/participante/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    /* EVIDENCIAS */
    fetchEvidencias(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-deportivo/actividad/${id}/evidencia`)
          .then(response => { ctx.commit('listarEvidencias', response.data.data); resolve(response) })
          .catch(error => reject(error))
      })
    },
    addEvidencia(ctx, data) {
      const config = { headers: { 'Content-Type': 'multipart/formdata' } }
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-deportivo/actividad/${data.get('id')}/evidencia`, data, config)
          .then(response => { resolve(response) })
          .catch(error => reject(error))
      })
    },
    deleteEvidencia(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .delete(`/servicio-deportivo/actividad/evidencia/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },
}
