<template>
  <div>
    <b-modal
      id="modalAddParticipante"
      ok-only
      size="lg"
      class="p-5"
      title="Buscar Estudiante"
    >
      <h2
        for=""
        class="mt-1 text-primary text-center"
      >BUSCAR PARTICIPANTES</h2>

      <b-row class="text-center">
        <b-col
          md="2"
          class="my-1"
        />
        <b-col
          md="8"
          class="my-1"
        >
          <b-form-group
            class="mb-0"
          >
            <b-input-group size="lg">
              <b-form-input
                v-model="cadena_a_buscar"
                type="search"
                placeholder="Buscar por DNI o Apellidos y nombres"
                @input="buscarParticipante"
              />

              <b-input-group-append>
                <b-button
                  variant="success"
                  size="sm"
                  @click="buscarParticipante"
                >
                  <feather-icon
                    icon="SearchIcon"
                    size="20"
                  />
                </b-button>
              </b-input-group-append>
            </b-input-group>
            <div v-if="mensajeMinimo">
              <label class="text-danger pt-1"><b-badge variant="light-danger">escribir mnimo 5 caracteres</b-badge> </label>
            </div>
            <div v-else-if="mensajeMinimo === false && m_participantes.length === 0">
              <label class="text-danger pt-1"><b-badge variant="light-warning">No se encontraron resultados</b-badge></label>
            </div>
          </b-form-group>
        </b-col>
        <b-col
          md="2"
          class="my-1"
        />
      </b-row>

      <b-table
        v-if="m_participantes.length>0"
        :items="m_participantes"
        :fields="m_fieldParticipantes"
        :busy.sync="isBusy"
        size="sm"
        sticky-header="300px"
        small
        head-variant="light"
      >
        <template #cell(persona)="data">
          <b-media vertical-align="center">
            <template #aside>
              <b-avatar
                size="32"
                :variant="`light-primary`"
                :text="avatarText(data.item.nombres)"
              />
            </template>
            <b-link
              class="font-weight-bold d-block text-nowrap"
            >
              {{ data.item.nombre_completo }}
            </b-link>
            <small class="text-muted">N DNI: {{ data.item.dni }}</small>
          </b-media>
        </template>
        <template #table-busy>
          <div class="text-center text-danger my-2">
            <b-spinner class="align-middle" />
            <strong>Cargando...</strong>
          </div>
        </template>
        <template #cell(actions)="row">
          <b-button
            size="sm"
            class="mr-1"
            variant="outline-success"
            @click="adicionarParticipante(row.item)"
          >
            registrar <feather-icon
              icon="CheckIcon"
              size="14"
            />
          </b-button>
        </template>
      </b-table>
      <!-- _______________ -->

      <template #modal-footer>
        <div class="w-100">
          <b-button
            variant="primary"
            size="sm"
            class="float-right"
            @click="$bvModal.hide('modalAddParticipante')"
          >
            Cerrar
          </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import {
  BButton, BFormGroup, BTable, BRow, BCol, BInputGroup, BFormInput,
  BInputGroupAppend, BMedia, BLink, BAvatar, BBadge,
} from 'bootstrap-vue'
import { mapState } from 'vuex'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import store from '@/store'
import { avatarText } from '@core/utils/filter'

export default {
  components: {
    BAvatar,
    BMedia,
    BLink,
    BButton,
    BFormGroup,
    BRow,
    BCol,
    BTable,
    BInputGroup,
    BInputGroupAppend,
    BFormInput,
    BBadge,
  },
  data() {
    return {
      avatarText,
      m_participantes: [],
      cadena_a_buscar: '',
      isBusy: false,
      mensajeMinimo: false,
      m_fieldParticipantes: [
        {
          key: 'persona', label: 'persona',
        },
        {
          key: 'pueblo_indigena', label: 'Pueblo Indigena', sortable: true, sortDirection: 'asc',
        },
        { key: 'actions', label: 'Acciones' },
      ],
    }
  },

  computed: {
    ...mapState('actividades_depor', ['participantes', 'tabla', 'talleresSegunDeporte', 'tituloModal', 'tituloBoton', 'guardarEditar', 'cursoTallerNuevo']),
  },
  methods: {
    buscarParticipante() {
      if (this.cadena_a_buscar.length < 5) {
        this.m_participantes = []
        this.mensajeMinimo = true
      } else if (this.cadena_a_buscar.length >= 5) {
        this.mensajeMinimo = false
        this.m_participantes = []

        clearTimeout(this.timeout)
        this.timeout = setTimeout(() => {
          store.dispatch('actividades_depor/searchParticipante', this.cadena_a_buscar).then(response => {
            response.data.data.forEach(value => {
              this.m_participantes.push({
                nombres: value.persona.nombres,
                nombre_completo: value.name,
                participante_id: value.id,
                dni: value.persona.numero_documento,
                participante: `${value.persona.apellido_paterno} ${value.persona.apellido_materno} ${value.persona.nombres}`,
                pueblo_indigena: value.persona.pueblo_indigena.nombre,
              })
            })
          }).catch(error => {
            console.log(error)
          })
        }, 500)
      } else {
        this.mensajeMinimo = true
      }
    },
    toastSuccess(text, tipo, titulo) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title: titulo,
          icon: 'BellIcon',
          text,
          variant: tipo,
        },
      })
    },
    adicionarParticipante(item) {
      const data = {
        actividad_id: store.state.actividades_depor.actividad_id,
        participante_id: item.participante_id,
      }
      if (this.verificar_existencia_participante(data)) {
        this.toastSuccess('Participante Duplicado!!', 'danger', 'Advertencia')
      } else {
        store.dispatch('actividades_depor/addParticipante', data).then(() => {
          this.toastSuccess('Participante agregado!!', 'success', 'xito')
          store.dispatch('actividades_depor/fetchParticipantes', store.state.actividades_depor.actividad_id)
        })
      }
    },
    verificar_existencia_participante(data) {
      const { participantes } = store.state.actividades_depor
      let resultado = false
      if (participantes !== undefined) {
        participantes.forEach(value => {
        // eslint-disable-next-line eqeqeq
          if (value.participante_id == data.participante_id) { resultado = true }
        })
      }
      return resultado
    },
  },

}
</script>

